//Controller.js
import React, { useState, useEffect } from "react";
import Modal from "react-modal";
import "../css/Controller.css";
import "../css/Title.css";
import "../css/Decade.css";
import "../css/Character.css";
import "../css/Keyword.css";
import "../css/People.css";
import "../css/SwitchAndDesignEditor.css";
import "../css/HelpModal.css";
import "../css/FontSelector.css";

const MovieFilter = ({
  categories,
  onFilterChange,
  handleReset,
  handleSizeChange,
  textSize,
  colorValue,
  currentColor,
  handleColorChange,
  style1Checked,
  style2Checked,
  style3Checked,
  style4Checked,
  style5Checked,
  style6Checked,
  handleStyle1Change,
  handleStyle2Change,
  handleStyle3Change,
  handleStyle4Change,
  handleStyle5Change,
  handleStyle6Change,
}) => {
  const [isHelpModalOpen, setIsHelpModalOpen] = useState(false);
  const openHelpModal = () => {
    console.log("도움 모달창 오픈");
    setIsHelpModalOpen(true);
  };
  const closeHelpModal = () => {
    setIsHelpModalOpen(false);
  };
  const HelpModalContent = (
    <div className="HelpModalContainer" onClick={closeHelpModal}>
      <div className="HelpDecade">
        {/* <span>⬋</span> */}
        <p> 빨간색 연도 버튼을 눌러보세요.</p>
      </div>
      <div className="HelpCharacter">
        {/* <span>⬋</span> */}
        <p>각 캐릭터의 버튼을 눌러보세요.</p>
      </div>
      <div className="HelpKeyword">
        {/* <span>⬋</span> */}
        <p>각 키워드의 버튼을 눌러보세요.</p>
      </div>
      <div className="HelpPeople">
        {/* <span>⬋</span> */}
        <p> 검색할 인명을 눌러보세요.</p>
      </div>
      <div className="HelpDesign">
        {/* <span>⬋</span> */}
        <p>
          {" "}
          글씨체, 글자 사이즈, 배경색을
          <br />
          변경해 보세요.
        </p>
      </div>
      <div className="HelpText">
        <p> 대사를 클릭해보세요.</p>
        <span>⬇</span>
      </div>
      {/* <button onClick={closeHelpModal}>도움말 닫기</button> */}
    </div>
  );

  const [filters, setFilters] = useState({});

  const handleCategoryChange = (changedCategory) => {
    setFilters((prevFilters) => {
      const updatedFilters = { ...prevFilters };
      for (const category in updatedFilters) {
        if (category !== changedCategory) {
          updatedFilters[category] = {};
        }
      }
      return updatedFilters;
    });
  };

  useEffect(() => {
    onFilterChange(filters);
  }, [filters]);
  // 체크박스 확인
  const handleCheckboxChange = (category, value) => {
    // console.log("Category:", category);
    // console.log("Value:", value);
    if (category === "year") {
      const startYear = Math.floor(value / 10) * 10;
      const endYear = startYear + 9;

      const yearFilters = {};
      for (let i = startYear; i <= endYear; i++) {
        yearFilters[i] = !filters[category]?.[i] || false;
      }

      setFilters((prevFilters) => ({
        ...prevFilters,
        [category]: yearFilters,
      }));
    } else {
      setFilters((prevFilters) => ({
        ...prevFilters,
        [category]: {
          ...prevFilters[category],
          [value]: !prevFilters[category]?.[value],
        },
      }));
    }
  };

  //////// 선택 항목 출력하기 ///////
  useEffect(() => {
    // 함수를 통해 선택된 필터를 확인하고 출력하는 부분
    const printSelectedFilters = (selectedFilters) => {
      for (const category in selectedFilters) {
        const selectedValues = Object.entries(selectedFilters[category])
          .filter(([value, isSelected]) => isSelected)
          .map(([value]) => `'${value}'`)
          .join(", ");

        const selectedValuesCount = Object.entries(
          selectedFilters[category]
        ).filter(([value, isSelected]) => isSelected).length;

        if (selectedValues.length > 0) {
          console.log(`${category} 중 ${selectedValues} 선택됨 `);
        }
      }
    };
    

    onFilterChange(filters); // 기존의 필터 변경 이벤트

    printSelectedFilters(filters); // 선택된 필터를 출력하는 부분
  }, [filters]);

  
  //////// 선택 항목 출력하기 끝 ///////

  const renderDecadeCheckboxes = () => {
    const checkboxes = [];
    for (let index = 0, decade = 1950; decade <= 2020; decade += 10, index++) {
      checkboxes.push(
        <div key={decade} className="DecadeWrapInner">
          <div className="Decade">
            <label className="switch" htmlFor={`DecadeInput_${index}`}>
              <input
                className="DecadeInput"
                type="checkbox"
                id={`DecadeInput_${index}`}
                onChange={() => {
                  handleCheckboxChange("year", decade);
                  handleCategoryChange("year");
                }}
                checked={
                  (filters.year &&
                    Array.from(
                      { length: 10 },
                      (_, i) => filters.year[decade + i]
                    )?.some(Boolean)) ||
                  false
                }
              />
              <div className="button">
                <span className="Param"> {`${decade}`}</span>
                <div className="light"></div>
                <div className="dots"></div>
                <div className="characters"></div>
                <div className="shine"></div>
                <div className="shadow"></div>
              </div>
            </label>
          </div>
        </div>
      );
    }

    return checkboxes;
  };

  return (
    <div>
      <section className="Controller">
        <section className="ContainerCtr">
          {/* <section className="Ctr_Intro">
            <div className="WrapTitle"></div>
            <div className="WrapIntro">
              <p className="TextIntro"></p>
              <div className="WrapGuide" onClick={openHelpModal}>
                <span className="subtitle">도움말</span>
                <p className="TextGuide">?</p>
              </div>
            </div>
          </section> */}
          {/*  * * * * * * * CtrA * * * * * *  */}
          <section className="CtrA">
            {/* 연도 섹션 */}
            <div className="DecadeSection">
              <h3 key="year-title" className="subtitle">
                연도 선택하기
              </h3>
              <div className="DecadeWrap">{renderDecadeCheckboxes()}</div>
            </div>
            {/* 캐릭터 섹션 */}

            <div className="CharacterSection">
              <h3 className="subtitle"> 캐릭터로 찾기 </h3>
              <div className="filter-section">
                <div className="CharacterWrap">
                  {categories.character.map((value, index) => (
                    <div className="Character" key={value}>
                      <input
                        type="checkbox"
                        id={`CharacterInput_${index}`}
                        checked={filters.character?.[value] || false}
                        onChange={() => {
                          handleCheckboxChange("character", value);
                          handleCategoryChange("character");
                        }}
                      />
                      <label
                        className="CharacterLabel"
                        htmlFor={`CharacterInput_${index}`}
                      ></label>
                      <span
                        className="Param"
                        onClick={() => {
                          const checkbox = document.getElementById(
                            `CharacterInput_${index}`
                          );
                          checkbox.checked = !checkbox.checked;
                          handleCheckboxChange("character", value);
                          handleCategoryChange("character");
                        }}
                      >
                        {value}
                      </span>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </section>
          {/*  * * * * * * * CtrB * * * * * *  */}
          <section className="CtrB">
            {/* 키워드 섹션 */}
            <div className="KeywordSection">
              <div className="filter-section">
                <h3 className="subtitle"> 키워드로 찾기</h3>
                <div className="KeywordWrap">
                  {categories.keyword.map((value, index) => (
                    <div key={value} className="Keyword">
                      <span className="Scope">ON</span>
                      <input
                        className="KeywordInput"
                        id={`KeywordInput${index}`}
                        type="checkbox"
                        checked={filters.keyword?.[value] || false}
                        onChange={() => {
                          handleCheckboxChange("keyword", value);
                          handleCategoryChange("keyword");
                        }}
                      />
                      <label
                        className="KeywordLabel"
                        htmlFor={`KeywordInput${index}`}
                      ></label>
                      <span
                        className="Param"
                        onClick={() => {
                          const checkbox = document.getElementById(
                            `KeywordInput${index}`
                          );
                          checkbox.checked = !checkbox.checked;
                          handleCheckboxChange("keyword", value);
                          handleCategoryChange("keyword");
                        }}
                      >
                        {value}
                      </span>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </section>
          {/*  * * * * * * * CtrC * * * * * *  */}
          <section className="CtrC">
            {/* 배우 섹션 */}
            <div className="filter-section PeopleSection">
              <div className="FilterActor">
                <h3 className="subtitle">배우</h3>
                <div className="PeopleWrap PeopleContainer" id="containerActor">
                  <div id="wrapActor" className="carousel">
                    {categories.actor.map((value, index) => (
                      <div
                        key={value}
                        className={`checkbox-div ${
                          filters.actor?.[value] ? "checked" : ""
                        }`}
                        onClick={() => {
                          handleCheckboxChange("actor", value);
                          handleCategoryChange("actor");
                        }}
                      >
                        <input
                          type="checkbox"
                          className="hidden-checkbox"
                          id={`ActorInput${index}`}
                          checked={filters.actor?.[value] || false}
                          onChange={() => {}}
                        />
                        <label
                          className={`PeopleInput checkbox-label ${
                            filters.actor?.[value] ? "checked" : ""
                          }`}
                          htmlFor={`ActorInput${index}`}
                        >
                          <span
                            className="Param"
                            onClick={() => {
                              handleCheckboxChange("actor", value);
                              handleCategoryChange("actor");
                            }}
                            style={{
                              fontSize: value.length > 5 ? "13px" : "16px",
                            }}
                          >
                            {value}
                          </span>
                        </label>
                      </div>
                    ))}
                  </div>
                </div>
              </div>
            </div>

            {/* 감독 섹션 */}
            <div className="filter-section PeopleSection">
              <div className="FilterDirector">
                <h3 className="subtitle">감독</h3>
                <div
                  className="PeopleWrap PeopleContainer"
                  id="containerDirector"
                >
                  <div id="wrapDirector" className="carousel">
                    {categories.director.map((value, index) => (
                      <div
                        key={value}
                        className={`checkbox-div ${
                          filters.director?.[value] ? "checked" : ""
                        }`}
                        onClick={() => {
                          handleCheckboxChange("director", value);
                          handleCategoryChange("director");
                        }}
                      >
                        <input
                          type="checkbox"
                          className="hidden-checkbox"
                          id={`DirectorInput${index}`}
                          checked={filters.director?.[value] || false}
                          onChange={() => {}}
                        />
                        <label
                          className={`PeopleInput checkbox-label ${
                            filters.director?.[value] ? "checked" : ""
                          }`}
                          htmlFor={`DirectorInput${index}`}
                        >
                          <span
                            className="Param"
                            onClick={() => {
                              handleCheckboxChange("director", value);
                              handleCategoryChange("director");
                            }}
                            style={{
                              fontSize: value.length > 5 ? "13px" : "16px",
                            }}
                          >
                            {value}
                          </span>
                        </label>
                      </div>
                    ))}
                  </div>
                </div>
              </div>
            </div>
            {/* 원작자 섹션 */}
            <div className="filter-section PeopleSection">
              <div className="FilterSource">
                <h3 className="subtitle">원작</h3>
                <div
                  className="PeopleWrap PeopleContainer"
                  id="containerSource"
                >
                  <div id="wrapSource" className="carousel">
                    {categories.source.map((value, index) => (
                      <div
                        key={value}
                        className={`checkbox-div ${
                          filters.source?.[value] ? "checked" : ""
                        }`}
                        onClick={() => {
                          handleCheckboxChange("source", value);
                          handleCategoryChange("source");
                        }}
                      >
                        <input
                          type="checkbox"
                          className="hidden-checkbox"
                          id={`SourceInput${index}`}
                          checked={filters.source?.[value] || false}
                          onChange={() => {}}
                        />
                        <label
                          className={`PeopleInput checkbox-label ${
                            filters.source?.[value] ? "checked" : ""
                          }`}
                          htmlFor={`SourceInput${index}`}
                        >
                          <span
                            className="Param"
                            onClick={() => {
                              handleCheckboxChange("source", value);
                              handleCategoryChange("source");
                            }}
                            style={{
                              fontSize:
                                value.length > 6
                                  ? "10px"
                                  : value.length > 4
                                  ? "14px"
                                  : "16px",
                            }}
                          >
                            {value}
                          </span>
                        </label>
                      </div>
                    ))}
                  </div>
                </div>
              </div>
            </div>
            {/* 각본 섹션 */}
            <div className="filter-section PeopleSection">
              <div className="FilterScenario">
                <h3 className="subtitle">각본</h3>
                <div
                  className="PeopleWrap PeopleContainer"
                  id="containerScenario"
                >
                  <div id="wrapScenario" className="carousel">
                    {categories.scenario.map((value, index) => (
                      <div
                        key={value}
                        className={`checkbox-div ${
                          filters.scenario?.[value] ? "checked" : ""
                        }`}
                        onClick={() => {
                          handleCheckboxChange("scenario", value);
                          handleCategoryChange("scenario");
                        }}
                      >
                        <input
                          type="checkbox"
                          className="hidden-checkbox"
                          id={`ScenarioInput${index}`}
                          checked={filters.scenario?.[value] || false}
                          onChange={() => {}}
                        />
                        <label
                          className={`PeopleInput checkbox-label ${
                            filters.scenario?.[value] ? "checked" : ""
                          }`}
                          htmlFor={`ScenarioInput${index}`}
                        >
                          <span
                            className="Param"
                            onClick={() => {
                              handleCheckboxChange("scenario", value);
                              handleCategoryChange("scenario");
                            }}
                            style={{
                              fontSize: value.length > 5 ? "13px" : "16px",
                            }}
                          >
                            {value}
                          </span>
                        </label>
                      </div>
                    ))}
                  </div>
                </div>
              </div>
            </div>
            {/* 각색 섹션 */}
            <div className="filter-section PeopleSection">
              <div className="FilterAdaptation">
                <h3 className="subtitle"> 각색</h3>
                <div
                  className="PeopleWrap PeopleContainer"
                  id="containerAdaptation"
                >
                  <div id="wrapAdaptation" className="carousel">
                    {categories.adaptation.map((value, index) => (
                      <div
                        key={value}
                        className={`checkbox-div ${
                          filters.adaptation?.[value] ? "checked" : ""
                        }`}
                        onClick={() => {
                          handleCheckboxChange("adaptation", value);
                          handleCategoryChange("adaptation");
                        }}
                      >
                        <input
                          type="checkbox"
                          className="hidden-checkbox"
                          id={`AdaptationInput${index}`}
                          checked={filters.adaptation?.[value] || false}
                          onChange={() => {}}
                        />
                        <label
                          className={`PeopleInput checkbox-label ${
                            filters.adaptation?.[value] ? "checked" : ""
                          }`}
                          htmlFor={`AdaptationInput${index}`}
                        >
                          <span
                            className="Param"
                            onClick={() => {
                              handleCheckboxChange("adaptation", value);
                              handleCategoryChange("adaptation");
                            }}
                            style={{
                              fontSize: value.length > 5 ? "13px" : "16px",
                            }}
                          >
                            {value}
                          </span>
                        </label>
                      </div>
                    ))}
                  </div>
                </div>
              </div>
            </div>
          </section>
          {/*  * * * * * * * CtrD * * * * * *  */}

          <section className="CtrD">
            <div className="SwitchOuter">
              <button className="BtnReset" onClick={handleReset}>
                <div className="ResetText">↪ 다시하기</div>
              </button>
              {/* <div className="WrapTitle"></div> */}
              <div className="WrapIntro">
                <p className="TextIntro"></p>
                <div className="WrapGuide" onClick={openHelpModal}>
                  <span className="subtitle"></span>
                  <p className="TextGuide">
                    <p>?</p>
                  </p>
                </div>
              </div>
            </div>
            {/* <h3 className="EditorSubtitle">디자인</h3> */}
            <h3 className="FontSubtitle">글씨체 선택</h3>
            <h3 className="SizeSubtitle">글씨크기</h3>
            <h3 className="FontBgSubtitle">배경색</h3>
            <div className="DesignEditor">
              <div className="container">
                {/* OUTER SHADOW */}
                <div className="de">
                  {/* TEXT */}
                  <div className="den">
                    {/* LINE */}
                    <hr className="line" />
                    <hr className="line" />
                    <hr className="line" />

                    {/* SWITCH */}
                    <div className="switch">
                      {/* TEXT */}
                      <label htmlFor="switch_1">
                        <span>1</span>
                      </label>
                      <label htmlFor="switch_2">
                        <span>2</span>
                      </label>
                      <label htmlFor="switch_3">
                        <span>3</span>
                      </label>
                      <label htmlFor="switch_4">
                        <span>4</span>
                      </label>
                      <label htmlFor="switch_5">
                        <span>5</span>
                      </label>
                      <label htmlFor="switch_6">
                        <span>6</span>
                      </label>

                      {/* INPUT */}
                      <input
                        id="switch_1"
                        type="radio"
                        name="switch"
                        checked={style1Checked}
                        onChange={() => handleStyle1Change()}
                      />
                      <input
                        id="switch_2"
                        type="radio"
                        name="switch"
                        checked={style2Checked}
                        onChange={() => handleStyle2Change()}
                      />
                      <input
                        id="switch_3"
                        type="radio"
                        name="fontStyle"
                        checked={style3Checked}
                        onChange={() => handleStyle3Change()}
                      />
                      <input
                        id="switch_4"
                        type="radio"
                        name="switch"
                        checked={style4Checked}
                        onChange={() => handleStyle4Change()}
                      />
                      <input
                        id="switch_5"
                        type="radio"
                        name="switch"
                        checked={style5Checked}
                        onChange={() => handleStyle5Change()}
                      />
                      <input
                        id="switch_6"
                        type="radio"
                        name="switch"
                        checked={style6Checked}
                        onChange={() => handleStyle6Change()}
                      />

                      {/* DOT */}
                      <div className="dot">
                        <span></span>
                      </div>

                      {/* CENTER */}
                      <div className="dene">
                        <div className="denem">
                          <div className="deneme"></div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              {/* * * * * * */}
              <div className="EditorWrap">
                <div className="Editor">
                  {/* Dynamically apply the size using inline styles */}
                  <span className="Scope">{textSize}</span>
                  <input
                    type="range"
                    className="EditorInput"
                    id="EditorID"
                    min="20"
                    max="200"
                    step="1"
                    value={textSize}
                    onChange={handleSizeChange}
                  />
                  <label className="EditorLabel" htmlFor="EditorID"></label>
                  <span className="Scope">0</span>
                </div>
                <div className="EditorBGColor">
                  {/* Dynamically apply the size using inline styles */}
                  {/* <span className="Scope">{colorValue}</span> */}
                  <input
                    type="range"
                    className="BGColorInput"
                    id="BGColorID"
                    min="1"
                    max="10"
                    step="0.1"
                    value={colorValue}
                    onChange={handleColorChange}
                  />
                  <label className="BGColorLabel" htmlFor="BGColorID"></label>
                </div>
              </div>
            </div>
          </section>
    
        </section>
        <Modal
          isOpen={isHelpModalOpen}
          onRequestClose={closeHelpModal}
          shouldCloseOnOverlayClick={true}
          contentLabel="도움말 모달"
          className="HelpModal"
          overlayClassName="HelpModalOverlay"
        >
          {HelpModalContent}
        </Modal>
      </section>
    </div>
  );
};

export default MovieFilter;
export const printSelectedFilters = (selectedFilters) => {
  for (const category in selectedFilters) {
    const selectedValues = Object.entries(selectedFilters[category])
      .filter(([value, isSelected]) => isSelected)
      .map(([value]) => `'${value}'`)
      .join(", ");

    if (selectedValues.length > 0) {
      console.log(`${category}: ${selectedValues} 선택된 export 존 `);
    }
  }
};
